import React from 'react';
import "animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';
import './Feature.css'


const Requestcall= ()=>{
	return(
			<div className='reqcall mt5' >
				<div style={{height:'300px'}}>
					<h2 className='tc f2 th-copy pt6 white-90 '>We Have A Passion To Deliver Great Results!</h2>
					<p className='tc white-90'>Are You Ready To Work With Whole New Concept Of Trading?</p>
				</div>
				<div className='tc'>
				 	<ScrollAnimation animateIn="bounce"  initiallyVisible={true} className='pa3'>
					    <button className='ttu bg-white-80 bb pointer link' style={{opacity:'0.6'}}>request call back</button>
					</ScrollAnimation>
				</div>
			</div>
		);
}

export default Requestcall;